.question-list-item {
    display: flex;

    .col-side { overflow: hidden; text-align: center; flex: 0 0 3rem; }
    .col-main { overflow: hidden; width: 100%; }
}

.btn-upvote, .btn-downvote {
    color: mix($gray-light, white, 30);
    @include hover-focus {
        color: $brand-primary;
        cursor: pointer;
    }
}
