.fixed-flex-container {
    display: inline-block;
    @include media-breakpoint-up(sm) { display: flex; }
    .col-fixed {
        flex: 0 1 auto;
        width: 100%;
        @include media-breakpoint-up(sm) { flex: 0 0 10rem; width: auto;}
        @include media-breakpoint-up(md) { flex: 0 0 11rem; }
        @include media-breakpoint-up(lg) { flex: 0 0 12rem; }
    }
    .col-flex { max-width: 40rem; width: 100%; }
}
