.home-cover {
    width: 100%;
    height: 32rem;
    padding-top: 8rem;

    color: white;

    background-image: url("/img/home-cover.png");
    background-size: cover;
    background-position: center;
}

.form-search {
    max-width: 36rem;
    margin: 0 auto;
}

.form-search-hint {
    padding: .3rem .5rem;
}

.input-search {
    max-width: 36rem;
    border-radius: 0;
    border-right: 0;
    background-color: transparentize(#eee, .1);
}

.input-group-btn-search {
    button {
        border-radius: 0;
    }
}

.btn-group-inline {
    display: inline;
    button {
        margin: 0 .5rem;
    }
}
