$divider-style: .0625rem solid mix($gray-light, white, 20);

.input-bundle-middle {
    margin-bottom: -1px;
    border-radius: 0;

    border-top: $divider-style;
    border-bottom: $divider-style;
    @include hover-focus {
        border-top: $divider-style;
        border-bottom: $divider-style;
    }
}

.input-bundle-top {
    margin-bottom: -1px;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;

    border-bottom: $divider-style;
    @include hover-focus {
        border-bottom: $divider-style;
    }
}

.input-bundle-bottom {
    background-color: mix($gray-lighter, white, 70);
    border: $border-width solid $input-border;
    border-bottom-left-radius: $input-border-radius;
    border-bottom-right-radius: $input-border-radius;

    border-top: $divider-style;
    @include hover-focus {
        border-top: $divider-style;
    }

    > button {

    }
}

.btn-post {
    border-radius: 0;
    border-bottom-right-radius: $input-border-radius;
}

.input-sm {
    font-size: .875rem;
    padding-left: .6rem;
    padding-right: .6rem;
}
